.intro {
  .bg, .bg > * {
    width: 100%;
    height: 100vh;
    height: -webkit-fill-available;
  }

  @keyframes intro-bg-show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .bg {
    .bg__video {
      object-fit: cover;
    }

    .bg__image {
      display: block;
      animation: intro-bg-show .3s ease-out;
    }
  }

  .login {
    position: absolute;
    top: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 30%;
    animation: intro-bg-show .3s ease-out;

    .login__sign-in {
      position: relative;
      width: 72%;
      height: 59px;
      border-radius: 12px;
      background-color: #fee500;
      border: 0;
      font-size: 1.3rem;
      color: #191919;

      &:before {
        position: absolute;
        content: '';
        top: 18px;
        left: 16px;
        width: 24px;
        height: 23px;
        background: url("../../assets/intro/kakao_ico.png") center center no-repeat;
        background-size: contain;
      }
    }

    .login__sign-up {
      width: 169px;
      height: 46px;
      border-radius: 7px;
      background-color: #2f2f2f;
      font-size: 0.9rem;
      color: white;
    }
  }

  .footer {
    position: absolute;
    left: 50%;
    bottom: 0;
    font-size: 0.8rem;
    transform: translateX(-50%);
    color: #3d3d3d;
  }
}
